import { BrowserModule } from '@angular/platform-browser';

import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';

import {RecordsService} from "./records.service";

import { HttpClientModule} from "@angular/common/http";

import {Data, RouterModule} from "@angular/router";
import {NgbModule} from "@ng-bootstrap/ng-bootstrap";
import {StoreModule} from "@ngrx/store";
import {reducers} from './store/reducers';
import {ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {MatInputModule} from '@angular/material/input';
import {MatSelectModule} from '@angular/material/select';
import {MatButtonModule} from '@angular/material/button';
import {MatCheckboxModule} from '@angular/material/checkbox';
import {MatChipsModule} from '@angular/material/chips';

import {AuthGuard} from "./auth.guard";
import {AuthService} from "./auth.service";
import {UserService} from "./user.service";


import { AppComponent } from './app.component';
import { HelloComponent } from './hello/hello.component';
import { DataComponent } from './data/data.component';
import { LoginComponent } from './login/login.component';
import { AdminComponent } from './admin/admin.component';
import { HomeComponent } from './home/home.component';
import { LogoutComponent } from './logout/logout.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { RegisterComponent } from './register/register.component';
import { HeaderComponent } from './header/header.component';
import { NewCompanyFormComponent } from './new-company-form/new-company-form.component';
import { NewEventFormComponent } from './new-event-form/new-event-form.component';

@NgModule({
  declarations: [
    AppComponent,
    HelloComponent,
    HomeComponent,
    DataComponent,
    LoginComponent,
    AdminComponent,
    LogoutComponent,
    DashboardComponent,
    RegisterComponent,
    HeaderComponent,
    NewCompanyFormComponent,
    NewEventFormComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    StoreModule.forRoot(reducers, {

    }),


    RouterModule.forRoot([
      {
        path: 'data',   // localhost:4200/data
        component: DataComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'logout',
        component: LogoutComponent
      },
      // {
      //   path: 'admin',
      //   component: AdminComponent,
      //   canActivate: [AuthGuard]
      // },
      {
        path: 'dashboard',
        component: DashboardComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'register',
        component: RegisterComponent
      },
      {
        path: '',
        component: HomeComponent
      },
      {
        path: 'newcompanyform',
        component: NewCompanyFormComponent
      }
    ]),
    NgbModule.forRoot(),
    BrowserAnimationsModule,
    ReactiveFormsModule,
    MatInputModule,
    MatButtonModule,
    MatSelectModule,
    MatCheckboxModule,
    MatChipsModule

  ],
  providers: [AuthService, UserService, AuthGuard, RecordsService],
  bootstrap: [AppComponent]
})
export class AppModule { }

